import React, { useState, useEffect, Fragment } from "react";
import { Spin } from "antd";
import axios from "axios";
import { GlobalStyle, Wrapper } from "./App.styles";

const App = () => {
  const [initialString, setInitialString] = useState("");
  const [fullSong, setFullSong] = useState([]);
  const [isLoading, setIsLoading] = useState(false);

  const getFullSong = async () => {
    try {
      setIsLoading(true);
      await axios
        .post(
          "http://localhost:5000/api/lyrics",
          {
            initial_string: initialString,
          },
          {
            headers: {
              "Content-Type": "application/json",
            },
          }
        )
        .then((response) => {
          setIsLoading(false);
          setFullSong(response.data.song);
        })
        .catch((err) => {
          console.log(err);
        });
    } catch (err) {
      console.log(err);
    }
  };

  return (
    <Fragment>
      <GlobalStyle />
      <div className="App">
        <Wrapper>
          <div className="page-container">
            <div className="content-wrap">
              <h1>Generate old hindi songs ...</h1>
              <p>
                The model uses character based prediction to generate song
                lyrics.
              </p>
              <p>Type some initial words to get a whole song !!</p>
              <div className="form-area">
                <div className="input-area">
                  <input
                    placeholder="Type initial words..."
                    value={initialString}
                    onChange={(e) => setInitialString(e.target.value)}
                  />
                </div>
                <div className="push-button">
                  <button onClick={() => getFullSong()}>
                    <span>Get Song</span>
                  </button>
                </div>
              </div>
              {isLoading ? (
                <div className="loading">
                  Lyrics are being generated and fetched ...
                </div>
              ) : (
                <div className="lyrics">
                  {fullSong.map((line) => (
                    <p>{line}</p>
                  ))}
                </div>
              )}
            </div>

            <div className="footer">
              <a href="https://github.com/standmarsh/old-hindi-song-generator">
                Code is linked at github
              </a>
            </div>
          </div>
        </Wrapper>
      </div>
    </Fragment>
  );
};

export default App;
